* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style: none;
}
header {
  color: white;
}
h1 {
  margin-top: -40px;
  vertical-align: start;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.header__wrap {
  height: 300px;
  background-image: url('./img/hphoto.jpg');
  margin: 0;
  padding: 100px 0 100px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.header__wrap::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: inherit;
  background-color: black;
  opacity: 0.5;
  margin: 0;
}
.search__wrap {
  width: 100%;
  vertical-align: center;
  text-align: center;
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: space-around;
}
.h1__container {
  margin-bottom: 40px;
}
.search-button {
  position: absolute;
  color: white;
  width: 50px;
  height: 50px;
  background-color: white;
}
.Filter {
  display: flex;
  flex-direction: row;
  width: 100%;
  text-align: center;
  margin-top: 30px;
  justify-content: center;
}
.sorting__wrap {
  display: flex;
  justify-content: space-between;
  margin-left: 65px;
}
.text__container {
  margin-right: 15px;
}
.searchbar {
  height: 30px;
  width: 450px;
  outline: none;
}
.category__wrap {
  display: flex;
  justify-content: space-between;
}
.searchButton {
  content: '';
  position: absolute;
  top: 50%;
  right: 556px;
  transform: translateY(-50%);
  width: 20px;
  height: 20px;
  cursor: pointer;
  background: url('./search.svg');
  background-repeat: no-repeat;
  background-size: contain;
  border: none;
}
.input__container {
  position: relative;
}
.input__container input[type='text'] {
  padding-right: 30px;
}
.result__container {
  color: black;
  margin-top: 100px;
  margin-left: 50px;
  text-align: left;
}

.book__card {
  display: flex;
  flex-direction: column;
  background-color: #f8f8f8;
  width: 25%;
}
.book__cards__container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
}
.book__thumbnail {
  text-align: center;
}
.category {
  text-decoration: underline;
  color: lightgray;
}
.author {
  color: lightgray;
}
.books__card__container {
  display: flex;
  flex-wrap: wrap;
  gap: 16px; /* Расстояние между карточками */
  justify-content: space-between;
}

.book__card {
  flex: 1 1 calc(25% - 16px); /* 25% ширины минус расстояние */
  box-sizing: border-box;
  max-width: calc(25% - 70px);
  margin-bottom: 20px;
  height: 350px;
  padding: 20px;
  margin-top: 45px;
}
.book__thumbnail {
  position: relative;
}
.book__thumbnail img {
  max-width: 100%;
  border-radius: 5px;
  height: 200px;
  border-radius: 5px;
  box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.2);
}
.book__info {
  margin-top: 10px;
}

.category {
  font-size: 14px;
  color: #888;
  margin-bottom: 5px;
}

.title {
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 5px;
}

.author {
  font-size: 14px;
  color: #555;
}

.total__books__container {
  text-align: center;
}

.hidden {
  opacity: 0;
}

.searchButton:disabled {
  cursor: not-allowed;
}

.categories__select {
  text-transform: uppercase;
  width: 200px;
  height: 30px;
  color: gray;
}
.sorting__select {
  text-transform: lowercase;
  width: 150px;
  height: 30px;
  color: gray;
}
.description {
  resize: none;
  width: 400px;
}
.book__details__card {
  display: flex;
  flex-direction: row;
  .book__details__thumbnail {
    width: 33%;
    height: 430px;
    margin-right: 20px;
    background-color: rgb(240, 240, 240);
    text-align: center;
  }
}
.book__details__thumbnail {
  img {
    width: 40%;
    object-fit: cover;
    transform: translateY(8%);
    vertical-align: center;
    border-radius: 5px;
    box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.2);
  }
}
.book__details__card {
.book__info {
  display: flex;
  flex-direction: column;
  height: 50%;
  background-color: #fff;
  justify-content: flex-start;
  align-items: flex-start;
  margin-left: 50px;
  .category {
    font-size: 14px;
    color: gray;
    text-decoration: none;
  }
  .title {
    font-size: 28px;
    font-weight: bold;
    margin: 10px 0;
    margin-top: 30px;
  }
  .author {
    font-size: 16px;
    color: #555;
    text-decoration: underline;
    color: #888;
  }
  .description {
    margin-top: 20px;
    margin-left: 0px;
    font-size: 14px;
    color: #666;
    border: 1px solid gray;
    width: 600px;
    height: 150px;
  }
}
}
.book__card {
  height: 100%;
  border-radius: 5px;
}
 
@media (min-width: 768px) {
  .book__details__card {
      flex-direction: row;
      align-items: flex-start;
  }
  .book__info {
      text-align: left;
      margin-left: 20px;
  }
}
.buttonBack {
  margin-left: -600px;
  margin-top: 380px;
  padding: 10px 20px;
  font-size: 16px;
  color: #fff;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.load__more {
  padding: 10px 20px;
  font-size: 16px;
  color: #fff;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  cursor: pointer;

}
.buttonBack:hover {
  background-color: #0056b3;
}
/* Extra small devices (phones, 320px and up) */
@media (min-width: 320px)  and (max-width:479px) {
  .book__info .title {
    font-size: 22px;
  }
  .book__info .description {
    max-width: 250px;
  }
  .book__details__card {
    justify-content: center;
    align-items: center;
    margin-top: 15px;
    border-radius: 5px;
    .category {
      width: 45%;
      margin: 0;
      margin-left: 55px;
      margin-top: 20px;
    }
    .buttonBack {
      margin-top: 50px;
      margin-left: 0px;
      margin-right: 45px;
    }
    .book__details__thumbnail {
      width: 100%;
      height: 170px;
      margin: 0;
      img {
        height: 75%;
        width: 30%;
        transform: translateY(10%);
      }
    }
  }
  .searchButton {
    right: 75px;
  }
  .search__wrap {
    margin-top: 75px;
  }
  .book__card {
    max-width: calc(75% - 10px);
    flex: 1 1 calc(75% - 16px);
  }
}
/* Small devices (phones, 480px and up) */
@media (min-width: 480px) and (max-width: 767px) {
  .book__info .title {
    font-size: 24px;
  }
  .book__info .description {
    max-width: 400px;
  }
  .book__details__card {
    margin-top: 15px;
    border-radius: 5px;
    .buttonBack {
      margin-top: 50px;
      margin-left: -250px;
    }
    .book__details__thumbnail {
      height: 250px;
      width: 50%;
    }
  }
  .searchButton {
    right: 125px;
  }
  .book__card {
    max-width: calc(75% - 30px);
    flex: 1 1 calc(75% - 36px);
  }
}
/* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) and (max-width: 1023px) {
  .book__details__card {
    flex-direction: row;
    align-items: flex-start;
    text-align: left;
  }
  .book__card {
    max-width: calc(50% - 50px);
    flex: 1 1 calc(50% - 56px);
  }
  .book__details__thumbnail {
    width: 33%;
    margin-right: 20px;
    height: 430px;
  }
  .book__info {
    align-items: flex-start;
  }
  .book__info .description {
    margin-left: 0;
    max-width: 500px;
  }
  .searchButton {
    right: 245px;
  }
  .book__details__card {
    .buttonBack {
      margin-right: -75px;
      margin-top: 80px;
    }
    .category {
      width: 50%;
    }
  .book__details__thumbnail {
     width: 50%;
     border-radius: 5px;
     margin-top: 15px;
     img {
      width: 40%;
      height: 56%;
      transform: translateY(38%);
     }
  }
}
}
/* Large devices (desktops, 1024px and up) */
@media (min-width: 1024px) and (max-width:1199px) {
  .book__info .title {
    font-size: 28px;
  }
  .book__info .description {
    max-width: 500px;
    
  }
  .book__details__card {
    .category {
       max-width: 50%;
    }
  }
  .searchButton {
    right: 300px;
  }
  .book__card {
    max-width: calc(33% - 70px);
    flex: 1 1 calc(33% - 76px);
  }
}
/* Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) and (max-width: 1400px) {
  #root {
    overflow: hidden;
  }
  .container {
    padding: 40px;
  }
  .book__details__thumbnail {
    height: 450px;
  }
  .searchButton {
    right: 386px;
  }
  .book__details__card {
    .category {
       max-width: 50%;
    }
  }
  .book__card {
    max-width: calc(33% - 70px);
    flex: 1 1 calc(33% - 76px);
  }
}
@media (max-width: 1200px) {
  .header__wrap {
    padding: 50px 0;
  }

  .searchbar {
    max-width: 100%;
  }
}

@media (max-width: 992px) {
  .book__details__card {
    flex-direction: column;
    align-items: center;
  }
  .book__info {
    align-items: center;
  }

  .description {
    width: 100%;
    max-width: none;
  }

  .Filter {
    flex-direction: column;
  }

  .sorting__wrap {
    margin-left: 0;
    margin-top: 10px;
  }
}

@media (max-width: 768px) {
  .header__wrap {
    padding: 30px 0;
  }

  .searchbar {
    width: 100%;
    max-width: 300px;
  }

  .input__container {
    width: 100%;
  }

  .Filter {
    flex-direction: column;
    align-items: center;
  }

  .category__wrap, .sorting__wrap {
    margin-top: 10px;
    justify-content: center;
  }
}

@media (max-width: 576px) {
  .header__wrap {
    padding: 20px 0;
  }

  .h1__container {
    margin-bottom: 10px;
  }

  .searchbar {
    max-width: 250px;
  }

  .Filter {
    width: 100%;
    justify-content: center;
  }
}

@media (max-width: 480px) {
  .book__info .title {
    font-size: 24px;
  }

  .book__info .author {
    font-size: 14px;
  }

  .book__info .description {
    font-size: 12px;
  }
}

@media (max-width: 320px) {
  .header__wrap {
    padding: 10px 0;
  }

  .h1__container h1 {
    font-size: 24px;
  }

  .searchbar {
    width: 100%;
    max-width: 200px;
  }

}